<template>
    <div class="classroomreport">
        <mt-header :title="navList[nowIndex].lan_name">
            <router-link to="/" slot="left">
                <mt-button icon="back"></mt-button>
            </router-link>
            <mt-button slot="right" v-if="nowIndex==0">个人信息</mt-button>
            <mt-button icon="more" slot="right" v-else></mt-button>
        </mt-header>

        <div class="navlist">
            <ul>
                <li class="navli" v-for="(item,index) in navList" :key="index" :class="{'activeT':nowIndex===index}" @click="tabClick(index)"><i>{{item.name}}</i></li>
            </ul>
        </div>
        <swiper :options="swiperOption" ref="mySwiper" @slideChangeTransitionEnd="touchEndFun">
            <!-- slides -->
            <swiper-slide>
                <div class="slide-itemBox slide-no1">
                    <div v-if="alertText==1" class="alert-text">请点击右上角“个人信息”完善信息，数据更准确</div>

                    <div class="sport-item" @click="tabClick(1)">
                        <div class="item-tit">
                            <div><img src="@/assets/img/icon_sport@2x.png" alt=""></div>
                            <div>今日步数<b class="text-big">2450</b></div>
                        </div>
                        <div class="item-con">
                            <ul>
                                <li></li>
                                <li></li>
                                <li></li>
                            </ul>
                            <ul>
                                <li></li>
                                <li></li>
                                <li></li>
                            </ul>
                        </div>
                    </div>

                    <div class="sleep-item" @click="tabClick(2)">
                        <div class="item-tit">
                            <div><img src="@/assets/img/icon_sleep@2x.png" alt=""></div>
                            <div>昨日睡眠<b class="text-big">7</b>小时<b class="text-big">20</b>分钟</div>
                        </div>
                        <div class="item-con">
                            <ul>
                                <li></li>
                                <li></li>
                                <li></li>
                            </ul>
                            <ul>
                                <li></li>
                                <li></li>
                                <li></li>
                            </ul>
                        </div>
                    </div>

                    <div class="heart-item" @click="tabClick(3)">
                        <div class="item-tit">
                            <div><img src="@/assets/img/icon_heart@2x.png" alt=""></div>
                            <div>当前心率<b class="text-big">59</b>次/分</div>
                        </div>
                        <div class="item-con">
                            <ul>
                                <li></li>
                                <li></li>
                                <li></li>
                            </ul>
                        </div>
                    </div>
                </div>
            </swiper-slide>
            <swiper-slide>
                <div class="slide-itemBox">
                    <div>
                        <div class="bg-img"><img src="@/assets/img/sport@2x.png" alt=""></div>
                    </div>
                </div>
            </swiper-slide>
            <swiper-slide>
                <div class="slide-itemBox">
                    <div>
                        <div class="bg-img"><img src="@/assets/img/sleep@2x.png" alt=""></div>
                    </div>
                </div>
            </swiper-slide>
            <swiper-slide>
                <div class="slide-itemBox">
                    <div>
                        <div class="bg-img"><img src="@/assets/img/heart@2x.png" alt=""></div>
                    </div>
                </div>
            </swiper-slide>
        </swiper>
    </div>
</template>

<script>
import 'swiper/dist/css/swiper.css'
import { swiper, swiperSlide } from 'vue-awesome-swiper'
export default {
    data() {
        return {
            // 页面提示
            alertText: 2,
            // tab数据
            navList:[
                {name:'总览',lan_name:'数据总览'},
                {name:'运动',lan_name:'今日运动'},
                {name:'睡眠',lan_name:'昨晚睡眠'},
                {name:'健康',lan_name:'今日健康数据'}
            ],
            // tab数据索引
            nowIndex:0,
            // swiper组件注册对象
            swiperOption:{
                initialSlide:0,
                autoplay:false,
                keyboardControl:true,
                autoHeight:true,
                observer:true,
                observeParents:true,
            }
        }
    },
    computed: {
        swiper() {
            return this.$refs.mySwiper.swiper
        }
    },
    components:{
        swiper,
        swiperSlide
    },
    mounted() {

    },
    methods: {
        tabClick(index){
            this.nowIndex = index
            this.swiper.slideTo(index,1000,false)
        },
        // 监听滑动
        touchEndFun(event){
            console.log(this.swiper.activeIndex)
            this.nowIndex = this.swiper.activeIndex
        }
    }
}
</script>

<style scoped>
    .classroomreport{
        width: 100%;height: 100%;
    }

    /* 导航tab */
    .navlist{
        width:100%;height:12vw;
        position:relative;
    }
    .navlist>ul{
        margin: 0;list-style: none;
        width: 100%;
        display: flex;
        justify-content: space-between;
        padding: 0 3vw;
        box-sizing: border-box;
    }
    .navli{
        width: 22vw;height: 9.08vw;
        box-sizing: border-box;
        display: flex;
        justify-content: center;
        align-items: center;
        border-radius: 1rem;
    }
    .navli i{
        font-style: normal;
        font-size: 3vw;
    }
    /* tab点击状态 */
    .activeT{
        color:#756AF0;
        background: url('../../assets/img/line_1@2x.png');
        background-size: cover;
    }
    /* tab容器 */
    .slide-itemBox{
        width: 100%;
        padding: 0 4vw 4vw 4vw;
        box-sizing: border-box;
    }

    /* 提示文字 */
    .alert-text{
        color: red;
        font-size: 3vw;
        display: flex;
        flex-direction: row!important;
        justify-content: start;
    }

    /* tab1 */
    .slide-no1>div{
        width: 100%;
        background-size: cover;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        align-items: center;
    }
    .slide-no1>div:not(:first-child){
        margin-top: 4vw;
    }
    .sport-item{
        height: calc(92vw / 1.9);
        background: url('../../assets/img/bg_sport@2x.png');
    }
    .item-tit{
        padding: 0 4vw;
        box-sizing: border-box;
        width: 100%;height: calc(92vw / 1.9 * 0.4);
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
    .item-tit>div:first-child{
        width: 7vw;height: 7vw;
    }
    .item-tit>div:first-child>img{
        width: 100%;height: 100%;
    }
    .item-tit>div:last-child{
        color: #fff;
        font-size: 3vw;
        letter-spacing: .5vw;
    }
    .text-big{
        font-size: 8vw;
        font-weight: 500;
        margin-left: .8vw;
    }

    .item-con{
        width: 100%;
    }
    .item-con>ul{margin: 0;padding: 0;list-style: none;}
    .sleep-item{
        height: calc(92vw / 1.9);
        background: url('../../assets/img/bg_sleep@2x.png');
    }
    .heart-item{
        height: calc(92vw / 2.59);
        background: url('../../assets/img/bg_heart@2x.png');
    }


    .bg-img{
        width: 100%;height: auto;
    }
    .bg-img>img{
        width: 100%;height: 100%;
    }
</style>



